import React from "react"
import { graphql, Link } from "gatsby"
import { ModalRoutingContext } from 'gatsby-plugin-modal-routing'

export default ({ data }) =>  (
  <ModalRoutingContext.Consumer>
    {({ modal, closeTo }) => (
      <div>
        {modal ? (
          <Link style={{ color: "white" }} to={closeTo}>
            Close
          </Link>
        ) : (
          <header style={{ color: "white" }}>
            <h1>
              Website Title
            </h1>
          </header>
        )}

        <div style={{ color: "white" }}>
          <img src={'../../' + data.illuminatorYaml.image}/>
          {data.illuminatorYaml.text}
        </div>
      </div>
    )}
  </ModalRoutingContext.Consumer>
)


export const query = graphql`
    query($name: String!) {
        illuminatorYaml(name: {eq: $name}) {
            image
            text
        }
    }
`
